.boxes {
  border: 1px solid lightgrey;
  height: 60vh;
  position: relative;
  overflow: hidden;
  user-select: none;
  touch-action: none;
}

.box {
  position: absolute;
  background-color: aliceblue;
  border: 2px solid lightblue;
  padding: 1.5rem;
  z-index: 1;
  user-select: none;
  cursor: pointer;
  font-size: 1rem;
}

.box.active {
  background-color: cornflowerblue;
  border-color: orangered;
  color: white;
  z-index: 2;
}

.box.editor {
  z-index: 3;
}

.box input {
  width: 100%;
}
